<template>
  <div class="nativeUtil">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;button onclick="getnativeUtil()"&gt;获取nativeUtil&lt;/button&gt;
&lt;script type="text/javascript"&gt;
    import nativeUtil from "cy_static/nativeUtil";
    nativeUtil.goBack();
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">nativeUtil 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import nativeUtil from "cy_static/nativeUtil"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    import nativeUtil from "cy_static/nativeUtil"; 
    nativeUtil.open("user", "closewebview");
    nativeUtil.goHome();
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>loadUrl</a>(url : String) : <a>Void</a></p>
                <p>通过Iframe加载URL加载完成删除Iframe；（主要用加载webView协议）</p>
              </li>
              <li>
                <p><a>open</a>(host : String = "user", action : String, data : Object, callback : function) : <a>Void</a></p>
                <p>调用javascript端注册的HandleName；（webView交互方式）</p>
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
										<th>参数名</th>
										<th>类型</th>
										<th>描述</th>
										<th class="text-c">必需</th>
									</tr>
									<tr>
										<td>host</td>
										<td class="atn">String</td>
										<td>注册的HandleName</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>action</td>
										<td class="atn">String</td>
										<td>注册的HandleName</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>data</td>
										<td class="atn">Object</td>
										<td>接口所需参数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>callback</td>
										<td class="atn">Function</td>
										<td>回调参数名称；需在window下</td>
										<td class="text-c">N</td>
									</tr>
                  <tr>
                    <td colspan="4">
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    nativeUtil.open("user", "closewebview", {
      title : "标题"
    }, HandleName);
&lt;/script&gt;
</xmp >
                    </td>
                  </tr>
                </table>
              </li>
              <li>
                <p><a>bridge</a> : Object</p>
                <p>bridge的方式与APP交互</p>
              </li>
              <li>
                <p><a>bridge.check</a>(callback : function) : <a>Void</a></p>
                <p>建立javascript与APP的沟通桥梁（bridge交互方式）</p>
              </li>
              <li>
                <p><a>bridge.open</a>(action : String, data : Object, callback : function) : <a>Void</a></p>
                <p>调用javascript端注册的HandleName（bridge交互方式）</p>
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
										<th>参数名</th>
										<th>类型</th>
										<th>描述</th>
										<th class="text-c">必需</th>
									</tr>
									<tr>
										<td>action</td>
										<td class="atn">String</td>
										<td>注册的HandleName</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>data</td>
										<td class="atn">Object</td>
										<td>接口所需参数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>callback</td>
										<td class="atn">Function</td>
										<td>回调参数</td>
										<td class="text-c">N</td>
									</tr>
                  <tr>
                    <td colspan="4">
<codemirror ref="demoCode2" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal2" style="display:none">
&lt;script type="text/javascript"&gt;
  nativeUtil.bridge.open({
      action : "share",
      data : {
          title : {
            othTitle : "标题"
          },
          content : "内容",
          imageurl : "路径",
          shareurl : "URL"
      },
      callback : function() {
      }
  });
&lt;/script&gt;
</xmp >
                    </td>
                  </tr>
                </table>
              </li>
              <li>
                <p><a><b>open_v1</b></a>(params : Object) : <a>Boolean</a></p>
                <p>调用javascript调用APP端注册的HandleName；兼容webView与bridge；返回值表示当前页面是否在畅由APP里打开；</p>
                
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
										<th>参数名</th>
										<th>类型</th>
										<th>默认值</th><th>描述</th>
										<th class="text-c">必需</th>
									</tr>
									<tr>
										<td>isVebView</td>
										<td class="atn">Boolean</td>
										<td>ios： <span class="atn">false</span>;android：<span class="atn">true</span>;</td><td>与App的交互方式</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>host</td>
										<td class="atn">String</td>
										<td>"user"</td><td>host名</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>action</td>
										<td class="atn">String</td>
										<td></td><td>注册的HandleName</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>data</td>
										<td class="atn">Object</td>
										<td></td><td>接口所需参数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>callback</td>
										<td class="atn">Function</td>
										<td></td><td>回调参数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>ios</td>
										<td class="atn">Object</td>
										<td>{}</td><td>在ios终端时的处理方式；参数和一级参数相同</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>android</td>
										<td class="atn">Object</td>
										<td>{}</td><td>在android终端时的处理方式；参数和一级参数相同</td>
										<td class="text-c">N</td>
									</tr>
                  <tr>
                    <td colspan="4">
<codemirror ref="demoCode3" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal3" style="display:none">
&lt;script type="text/javascript"&gt;
  nativeUtil.bridge.open({
      action : "share",
      data : {
          title : {
            othTitle : "标题"
          },
          content : "内容",
          imageurl : "路径",
          shareurl : "URL"
      },
      callback : function() {
      }
  });
&lt;/script&gt;
</xmp >
                    </td>
                  </tr>
                </table>
              </li>
              <li>
                <p><a>closeWebview</a>(param : Object = null) : <a>Boolean</a></p>
                <p>关闭APP，参数与open_v1相同；</p>
              </li>
              <li>
                <p><a>goBack</a>() : <a>Void</a></p>
                <p>后退调用history.back；APP调用上述closeWebview方法</p>
              </li>
              <li>
                <p><a>goHome</a>(url : String = "/jfmall/index.htm") : <a>Void</a></p>
                <p>返回首页；APP调用上述closeWebview方法</p>
              </li>
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {showDetail,trim} from "../../../assets/js/codeStyle";
export default {
  name: 'nativeUtil',
  data () {
    return {
      msg: 'nativeUtil',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:240px}
  .cy_example .iframeBox{flex:10}
</style>



